<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-table :data="databaseLists" border>
            <el-table-column label="" prop="name"></el-table-column>
            <el-table-column label="" prop="version"></el-table-column>
            <el-table-column label="" prop="engine"></el-table-column>
            <el-table-column label="" prop="data_length"></el-table-column>
            <el-table-column label="" prop="auto_increment"></el-table-column>
            <el-table-column label="" prop="collation"></el-table-column>
            <el-table-column label="" prop="comment"></el-table-column>
            <el-table-column label="" sortable prop="create_time"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="primary" plain size="mini" @click="updateAuth(scope.row)"> </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import $url from '../../api/url';
    export default {
        name: "lists",
        data(){
            return {
                databaseLists:[],
                labelWidth:'80px',
                loading:true,
                loadingText:'',

                cgi:{

                },
            }
        },
        methods:{
            /**
             * todo
             */
            getDatabaseLists:function () {
                apiLists.DatabaseLists([]).then(response=>{
                    if (response.data.code === 200){
                        this.databaseLists = response.data.item;
                        this.loading = false;
                    }
                });
            },
        },
        mounted() {
            this.$nextTick(function () {
                this.getDatabaseLists()
            });
        }
    }
</script>

<style scoped>

</style>



