const md5 = require('js-md5');
/**
 * todo
 * @param str
 * @param char
 * @returns {number}
 */
const str_count=function (str, char) {
    let pos;
    let arr = [];
    pos = str.indexOf(char);
    while (pos > -1) {
        arr.push(pos);
        pos = str.indexOf(char, pos + 1);
    }
    return arr.length;
};
/**
 * todo
 * @param times
 * @param timestamp
 * @returns {string}
 */
const t = function(times = true,timestamp){
    let date = new Date(timestamp);
    let Y = date.getFullYear();  // (4,1970)
    let M = (date.getMonth()+1)<10?'0'+eval(date.getMonth()+1):date.getMonth()+1;  // (0-11,01,1)
    let D = (date.getDate()<10)?'0'+date.getDate():date.getDate();  // (1-31)
    let h = (date.getHours()<10)?'0'+date.getHours():date.getHours();  // (0-23)
    let m = (date.getMinutes())<10?'0'+date.getMinutes():date.getMinutes();  // (0-59)
    let s = (date.getSeconds()<10)?'0'+date.getSeconds():date.getSeconds();  //
    if (times){
        return Y+'-'+M+'-'+D+' '+h+':'+m+':'+s;
    } else {
        return Y+'-'+M+'-'+D+'.log';
    }
};

/**
 * todo
 * @return {number}
 */
const get_timestamp=function() {
    return Math.round(new Date().getTime()/1000);
};
/**
 * todo
 * @param timestamp
 * @returns {string}
 */
const set_time=function(timestamp){
    return t(true,timestamp);
};

/**
 * todo
 * @param str
 * @param char
 * @param repeatStr
 * @return {string}
 */
const repeat_str=function(str,char,repeatStr=''){
    let count = str_count(str,char);
    return Array(count).join(repeatStr)
};

/**
 * todo
 * @param item
 * @param pid
 * @return {Array}
 */
const set_tree=function (item,pid=0){
    const arr = [];
    for (const i in item){
        if (item[i].pid === pid) {
            item[i].__child = set_tree(item,item[i].id);
            arr.push(item[i]);
        }
    }
    return arr;
};

/**
 * todo
 * @param length 
 * @param type 
 * @return {string|string}
 */
const set_random=function (length = 8,type='all'){
    let str;
    switch (type) {
        case 'all': str = 'MNBVCXZASDFGHJKLPOIUYTREWQ0123456789qwertyuioplkjhgfdsazxcvbnm';break;
        case 'number': str='0123456789';break;
        case 'l': str='QWERTYUIOPLKJHGFDSAZXCVBNM';break;
        case 's': str='zxcvbnmlkjhgfdsaqwertyuiop';break;
        default:break;
    }
    let char='';
    for (let i=0;i<length;i++){
        char+=str_shuffle(str).substring(0,1);
    }
    return char
};

/**
 * todo
 * @param str 
 * @return {string}
 */
const str_shuffle=function(str){
    let char='';
    for (let i=str.length;i>0;i--){
        let j = Math.random()*(i+1)|0;
        char+=str[j];
    }
    return char
};

/**
 * todo
 * @param pass
 * @param slat
 * @return {string}
 */
const set_password=function(pass,slat){
    return md5(md5(pass)+md5(slat));
};
const func = {
    str_count:str_count,
    set_time:set_time,
    repeat_str:repeat_str,
    set_tree:set_tree,
    get_timestamp:get_timestamp,
    set_random:set_random,
    str_shuffle:str_shuffle,
    set_password:set_password,
};
module.exports = func;


