<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-form :inline="true" style="margin-top: 10px">
            <el-form-item style="float:right;">
                <el-button icon="el-icon-plus" type="primary" size="medium" plain @click="addRole"> </el-button>
            </el-form-item>
        </el-form>
        <!--table -->
        <el-table :data="roleLists" border>
            <el-table-column label="#" prop='id' sortable></el-table-column>
            <el-table-column label="" prop="role_name" ></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <Radio :item="scope.row" :url="cgi.status"></Radio>
                </template>
            </el-table-column>
            <el-table-column label="" sortable prop="created_at"></el-table-column>
            <el-table-column label="" sortable prop="updated_at"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="primary" plain icon="el-icon-edit" size="mini" @click="updateRole(scope.row)"> </el-button>
                    <Delete :url="cgi.remove" :item="scope.row" :index="scope.$index" :Lists="roleLists"></Delete>
                </template>
            </el-table-column>
        </el-table>
        <!--table -->
        <!----->
        <el-dialog :title="title" :visible.sync="syncVisible" :modal="modal"  center>
            <el-form :label-width="labelWidth" :model="roleModel" :ref="reFrom" :rules="rules">
                <el-form-item label="" prop="role_name">
                    <el-input v-model="roleModel.role_name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="auth_ids">
                    <el-transfer
                        :titles="['', '']"
                        :button-texts="['', '']"
                        v-model="defaultChecked"
                        :data="authLists"
                        filterable
                        @change="handleChange">
                    </el-transfer>
                </el-form-item>
                <el-form-item label="" prop="status" v-if="act === 'add'">
                    <el-radio-group v-model="roleModel.status" size="small">
                        <el-radio-button label="0"></el-radio-button>
                        <el-radio-button label="1"></el-radio-button>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <Submit :reFrom="reFrom" :model="roleModel" :url="url" :refs="refs" v-on:success="success"></Submit>
            </div>
        </el-dialog>
        <!----->
    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import $url from '../../api/url';
    import func from '../../api/func'
    import Radio from "../common/Radio";
    import Delete from "../common/Delete";
    import Submit from "../common/Submit";
    export default {
        name: "lists",
        components: {Submit, Delete, Radio},
        data(){
            return {
                roleLists:[],
                loading:true,
                loadingText:'',
                authLists:[],
                defaultChecked:[],

                title:'',
                syncVisible:false, //
                modal:true, //
                labelWidth:'80px',

                url:'',
                refs:this.$refs,
                reFrom:'role',
                act:'',
                roleModel:{},

                ids:[],
                urls:[],

                cgi:{
                    insert:$url.roleSave,
                    update:$url.roleUpdate,
                    remove:$url.roleDelete,
                    status:$url.roleUpdate
                },
                rules:{
                    role_name:[{ required:true,message:'',trigger:'blur' }],
                    ids:[{ required:true,message:'ID',trigger:'blur' }],
                    urls:[{ required:true,message:'',trigger:'blur' }]
                },
            }
        },
        methods:{
            /**
             * todo
             */
            success:function(){
                this.getRoleLists(this.page,this.limit)
                this.syncVisible = false;
            },
            /**
             * todo
             * @param page
             * @param limit
             */
            getRoleLists:function (page,limit) {
                let params = { offset:limit*(page-1), limit:limit,level:4 };
                apiLists.RoleLists(params).then(response=>{
                    if (response.data.code===200){
                        this.roleLists = response.data.item.role;
                        this.authLists = response.data.item.auth
                        this.loading = false;
                    }
                })
            },
            /**
             * todo
             * @param val
             */
            sizeChange:function(val){
                this.limit = val;
                this.getRoleLists(this.page,this.limit)
            },
            /**
             * todo
             * @param val
             */
            currentChange:function(val){
                this.page = val;
                this.getRoleLists(this.page,this.limit)
            },
            /**
             * todo
             */
            addRole:function () {
                this.title='';
                this.syncVisible = true;
                this.url = this.cgi.insert;
                this.act = 'add';
                this.defaultChecked = [];
                this.roleModel={
                    role_name:'',
                    auth_ids:[],
                    auth_url:[],
                    status:'1',
                    created_at:func.get_timestamp(),
                    updated_at:func.get_timestamp()
                }
            },
             /**
             * @param value      
             * @param direction  'left' / 'right'
             * @param movedKeys   key 
             */
            handleChange(value, direction, movedKeys) {
                let __this = this;
                switch (direction) {
                    //
                    case 'left':
                        movedKeys.forEach(function (item,index) {
                            __this.ids.splice(__this.ids.indexOf(item),1);
                        });
                        break;
                    //
                    case 'right':
                        movedKeys.forEach(function (item,index) {
                            __this.ids.push(parseInt(item));
                        });
                        break;
                }
                __this.roleModel.auth_ids = __this.ids;
            },
            /**
             * todo
             * @param item
             */
            updateRole:function (item) {
                this.syncVisible = true;
                this.title = '';
                this.url = this.cgi.update;
                this.ids = JSON.parse(item.auth_ids);
                this.urls = JSON.parse(item.auth_url);
                this.defaultChecked = []; //
                this.act = 'update';
                for (let i in this.ids){
                    this.defaultChecked.push(parseInt(this.ids[i]));
                }
                this.roleModel = item;
                this.roleModel.auth_ids = this.defaultChecked;
            },
        },
        mounted() {
            this.$nextTick(function () {
                this.getRoleLists(this.page,this.limit)
            });
        }
    }
</script>

<style scoped>

</style>



